import { useState, useCallback } from 'react';

// Define the severity type
export type SnackbarSeverity = 'success' | 'error' | 'info' | 'warning';

// Define the hook's return type
interface UseSnackbarReturn {
  snackbar: {
    open: boolean;
    message: string;
    severity: SnackbarSeverity;
  };
  showSnackbar: (message: string, severity?: SnackbarSeverity) => void;
  closeSnackbar: () => void;
}

// Define the custom hook
export const useSnackbar = (): UseSnackbarReturn => {
  const [open, setOpen] = useState(false);
  const [message, setMessage] = useState('');
  const [severity, setSeverity] = useState<SnackbarSeverity>('success');

  const showSnackbar = useCallback((msg: string, sev: SnackbarSeverity = 'success') => {
    setMessage(msg);
    setSeverity(sev);
    setOpen(true);
  }, []);

  const closeSnackbar = useCallback(() => {
    setOpen(false);
    // Optionally reset message/severity on close, or let them persist until next showSnackbar
    // setMessage(''); 
    // setSeverity('success');
  }, []);

  return {
    snackbar: {
      open,
      message,
      severity,
    },
    showSnackbar,
    closeSnackbar,
  };
}; 