import React from 'react';
import MuiAlert, { AlertProps } from '@mui/material/Alert';

// Define the SnackbarAlert component using forwardRef
const SnackbarAlert = React.forwardRef<HTMLDivElement, AlertProps>(
  function SnackbarAlert(props, ref) {
    // Use the standard imported MuiAlert
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
  }
);

export default SnackbarAlert;

// Add empty export to ensure it's treated as a module under --isolatedModules
export {}; 